:root {
    --app-height: 100vh;
    --theme-bg-color: 245, 245, 245;
    --theme-bg-secondary-color: 255, 255, 255;
    --primary-color: 37, 37, 37;
    --logo-filter: invert(0);
    --dropdown-bg-color: 238, 238, 238;
    --select-bg-color: 233, 233, 233;
    --custom-scroll-bg: 211, 211, 211;
    --custom-scroll: 124, 124, 124;
    --custom-range-dots: 255, 255, 255;
    --custom-range-mini-dots: 216, 217, 224;
    --custom-range-border: 42, 174, 125;
    --bg-picture: 250, 250, 250;
    --exports-color: 41, 21, 227
}

[data-theme=light] {
    --theme-bg-color: 245, 245, 245;
    --theme-bg-secondary-color: 255, 255, 255;
    --primary-color: 37, 37, 37;
    --logo-filter: invert(0);
    --dropdown-bg-color: 238, 238, 238;
    --select-bg-color: 233, 233, 233;
    --custom-scroll-bg: 211, 211, 211;
    --custom-scroll: 124, 124, 124;
    --custom-range-dots: 255, 255, 255;
    --custom-range-mini-dots: 216, 217, 224;
    --custom-range-border: 42, 174, 125;
    --bg-picture: 250, 250, 250;
    --exports-color: 41, 21, 227
}

[data-theme=dark] {
    --theme-bg-color: 15, 19, 35;
    --theme-bg-secondary-color: 23, 29, 52;
    --primary-color: 255, 255, 255;
    --logo-filter: invert(1);
    --dropdown-bg-color: 42, 47, 68;
    --select-bg-color: 42, 47, 68;
    --custom-scroll-bg: 124, 124, 124;
    --custom-scroll: 211, 211, 211;
    --custom-range-dots: 42, 174, 125;
    --custom-range-mini-dots: 79, 84, 101;
    --custom-range-border: 255, 255, 255;
    --bg-picture: 33, 41, 70;
    --exports-color: 255, 255, 255
}

@font-face {
    font-family: "Proxima Nova";
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: url(../fonts/ProximaNova-Regular.ttf) format("truetype")
}

@font-face {
    font-family: "Proxima Nova";
    font-style: normal;
    font-weight: 500;
    font-display: swap;
    src: url(../fonts/ProximaNova-Semibold.ttf) format("truetype")
}

@font-face {
    font-family: "Proxima Nova";
    font-style: normal;
    font-weight: 600;
    font-display: swap;
    src: url(../fonts/ProximaNova-Bold.ttf) format("truetype")
}

@font-face {
    font-family: "Proxima Nova";
    font-style: normal;
    font-weight: 700;
    font-display: swap;
    src: url(../fonts/ProximaNova-Extrabold.ttf) format("truetype")
}

.page {
    box-sizing: border-box;
    min-width: 320px;
    height: 100%
}

.page__body {
    position: relative
}

*, :after, :before {
    box-sizing: inherit
}

* {
    -webkit-tap-highlight-color: transparent
}

body {
    display: flex;
    flex-direction: column;
    min-height: 100%;
    margin: 0;
    word-break: break-word;
    font-family: "Proxima Nova", -apple-system, blinkmacsystemfont, "Roboto", "Ubuntu", "Droid Sans", "Helvetica Neue", "Arial", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    font-size: 16px;
    line-height: 1.5;
    text-align: left;
    color: #22232f;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}

[tabindex='-1']:focus {
    outline: 0 !important
}

[role=button] {
    cursor: pointer
}

[type=search]::-webkit-search-decoration {
    -webkit-appearance: none
}

[role=button], a, button, input, label, select, textarea {
    touch-action: manipulation;
    margin: 0
}

button, input {
    overflow: visible;
    text-transform: none
}

[type=reset], [type=submit], button, html [type=button] {
    -webkit-appearance: button
}

[type=button]::-moz-focus-inner, [type=reset]::-moz-focus-inner, [type=submit]::-moz-focus-inner, button::-moz-focus-inner {
    padding: 0;
    border-style: none
}

button {
    padding: 0;
    background-color: transparent;
    cursor: pointer;
    border: 0;
    outline: 0;
    font-family: inherit
}

input[type=date], input[type=datetime-local], input[type=month], input[type=time] {
    -webkit-appearance: listbox
}

textarea {
    overflow: auto;
    resize: vertical
}

svg {
    fill: currentColor;
    vertical-align: middle
}

a svg, button svg {
    pointer-events: none
}

svg:not(:root) {
    overflow: hidden
}

img {
    max-width: 100%;
    height: auto
}

a {
    font-family: inherit;
    color: inherit;
    text-decoration: none;
    background-color: transparent;
    transition: color .3s
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
    font-family: "Proxima Nova", -apple-system, blinkmacsystemfont, "Roboto", "Ubuntu", "Droid Sans", "Helvetica Neue", "Arial", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    font-weight: 700;
    line-height: 1.2;
    color: currentColor
}

h1 {
    font-size: 40px
}

h2 {
    font-size: 32px
}

h3 {
    font-size: 24px
}

h4 {
    font-size: 18px
}

h5, h6 {
    font-size: 16px
}

address, blockquote, dl, figure, pre, table {
    margin-top: 0
}

ol, p, ul {
    margin: 0
}

address {
    font-style: normal
}

ol, ul {
    list-style: none;
    padding: 0
}

li {
    line-height: inherit
}

small, sub, sup {
    font-size: .75em
}

b, strong {
    font-weight: 500
}

blockquote {
    padding: 0;
    margin-left: 0;
    margin-right: 0
}

code, pre {
    background-color: rgba(0, 0, 0, .08)
}

code {
    padding: .06em .3em;
    color: #22232f;
    border-radius: 10px
}

pre {
    display: block;
    width: 100%;
    overflow-x: auto;
    -moz-tab-size: 2;
    -o-tab-size: 2;
    tab-size: 2
}

pre code {
    background: 0 0
}

.box {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px
}

@media screen and (min-width:768px) {
    .box {
        padding: 0 30px
    }
}

.subtitle {
    font-weight: 700;
    font-size: 14px;
    line-height: 35px;
    letter-spacing: .2em;
    text-transform: uppercase
}

@media screen and (min-width:768px) {
    .subtitle {
        font-size: 16px;
        line-height: 35px
    }
}

.title {
    font-weight: 500;
    font-size: 28px;
    line-height: 35px
}

@media screen and (min-width:768px) {
    .title {
        font-size: 42px;
        line-height: 45px
    }
}

.no-scroll {
    overflow: hidden
}

.no-scroll:before {
    position: absolute;
    content: "";
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .6);
    z-index: 10
}

.text-transform-uppercase {
    text-transform: uppercase
}

.highlight-text {
    color: #38c6dd
}

.btn {
    display: inline-block;
    text-align: center;
    border: 1px solid transparent;
    cursor: pointer;
    transition: all .3s
}

.btn--green {
    color: #fff;
    background-color: #24ac80
}

.btn--blue:hover, .btn--green:hover {
    color: #24ac80;
    background-color: transparent;
    border-color: #24ac80
}

.btn--blue {
    color: #fff;
    background-color: #2951e3
}

.btn--blue:hover {
    color: #2951e3;
    border-color: #2951e3
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
    white-space: nowrap;
    -webkit-clip-path: inset(100%);
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden
}

.h1 {
    margin: 30px 0 10px;
    font-weight: 700;
    font-size: 20px;
    line-height: 20px
}

@media screen and (min-width:768px) {
    .h1 {
        margin-top: 35px;
        margin-bottom: 15px;
        font-size: 28px;
        line-height: 28px
    }
}

.h2 {
    margin: 20px 0 10px;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px
}

@media screen and (min-width:768px) {
    .h2 {
        font-size: 18px;
        line-height: 27px
    }
}

.text-align-right {
    text-align: right
}

.text-align-left {
    text-align: left
}

.text-align-center {
    text-align: center
}

.text-muted {
    font-weight: 400;
    color: rgba(var(--primary-color), .5)
}

.color-red {
    color: #ef4f46
}

.color-green {
    color: #24ac80
}

.color-yellow {
    color: #ffbb36
}

.show-icon {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    right: 10px;
    cursor: pointer;
    fill: #9599a9
}

.btn-green {
    color: #fff;
    background-color: #24ac80;
    transition: background .3s, border .3s, color .3s
}

.btn-blue:hover, .btn-green:hover {
    color: #24ac80;
    border-color: #24ac80;
    background-color: transparent
}

.btn-blue, .btn-green, .btn-primary {
    border: 1px solid transparent;
    border-radius: 2px
}

.btn-blue {
    color: #fff;
    background-color: #2951e3;
    transition: background .3s, border .3s, color .3s
}

.btn-blue:hover {
    color: #2951e3;
    border-color: #2951e3
}

.btn-primary {
    color: rgba(var(--primary-color), 1);
    background-color: rgba(var(--primary-color), .1);
    transition: background .3s, border .3s
}

.btn-primary:hover {
    border-color: rgba(var(--primary-color), 1);
    background-color: transparent
}

.custom-scroll {
    overflow: auto
}

.bv_ul_inner::-webkit-scrollbar, .custom-scroll::-webkit-scrollbar, .dashboard::-webkit-scrollbar, .tooltip__text::-webkit-scrollbar {
    width: 3px;
    height: 3px;
    background-color: rgba(var(--custom-scroll-bg), 1)
}

.bv_ul_inner::-webkit-scrollbar-track, .custom-scroll::-webkit-scrollbar-track, .dashboard::-webkit-scrollbar-track, .tooltip__text::-webkit-scrollbar-track {
    background-color: rgba(var(--custom-scroll-bg), 1);
    border-radius: 999px
}

.bv_ul_inner::-webkit-scrollbar-thumb, .custom-scroll::-webkit-scrollbar-thumb, .dashboard::-webkit-scrollbar-thumb, .tooltip__text::-webkit-scrollbar-thumb {
    background-color: rgba(var(--custom-scroll), 1);
    border-radius: 999px
}

.theme {
    background-color: rgba(var(--theme-bg-color), 1);
    color: rgba(var(--primary-color), 1)
}

.theme, .theme>* {
    transition: color .3s, background .3s
}

.dashboard {
    background-color: rgba(var(--theme-bg-color), 1);
    color: rgba(var(--primary-color), 1)
}

.dashboard .page-main {
    padding: 50px 10px 20px
}

@media screen and (min-width:768px) {
    .dashboard .page-main {
        padding: 60px 20px 20px
    }
}

.dashboard-trade .dashboard-header__content {
    max-width: 1920px
}

.dashboard-trade .page-main {
    padding: 50px 0 20px
}

@media screen and (min-width:768px) {
    .dashboard-trade .page-main {
        padding: 60px 0 20px
    }
}

.tooltip {
    padding: 0 5px;
    position: relative;
    cursor: pointer
}

.tooltip--show .tooltip__text {
    opacity: 1;
    visibility: visible;
    transform: scaleY(1)
}

.tooltip__icon {
    margin-top: -2px;
    border: 1px solid rgba(var(--primary-color), .5);
    border-radius: 50%
}

.tooltip__text {
    opacity: 0;
    visibility: hidden;
    transform: scale(0);
    position: absolute;
    top: 25px;
    right: 0;
    padding: 10px;
    width: 200px;
    max-height: 300px;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.2;
    color: rgba(var(--primary-color), 1);
    background-color: rgba(var(--dropdown-bg-color), 1);
    border: 1px solid rgba(var(--theme-bg-secondary-color), 1);
    border-radius: 10px;
    z-index: 2;
    transform-origin: top;
    transition: all .3s;
    overflow: auto
}

.logo {
    display: inline-flex;
    align-items: center
}

.logo__icon {
    margin-right: 10px
}

.logo__text {
    filter: invert(1)
}

.theme__button {
    position: absolute;
    display: flex;
    align-items: center;
    top: 20px;
    right: 20px;
    color: rgba(var(--primary-color), 1)
}

@media screen and (min-width:1280px) {
    .theme__button {
        position: relative;
        top: 0;
        right: 0
    }
}

.theme__button-text {
    margin-right: 10px
}

.theme__button-input {
    display: none
}

.theme__button-label, .theme__button-label:after {
    position: relative;
    display: block;
    transition: all .3s
}

.theme__button-label {
    background-color: #fff;
    border: 1px solid rgba(var(--primary-color), .2);
    border-radius: 999px;
    padding: 2px;
    outline: 0;
    width: 35px;
    height: 20px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none
}

.theme__button-label:after {
    content: "";
    width: 50%;
    height: 100%;
    left: 0;
    border-radius: 50%;
    background-color: rgba(var(--primary-color), .2)
}

.theme__button-input:checked+.theme__button-label {
    background-color: #24ac80
}

.theme__button-input:checked+.theme__button-label:after {
    left: 50%;
    background-color: #fff
}

.dashboard-header {
    position: fixed;
    width: 100%;
    background-color: rgba(var(--theme-bg-secondary-color), 1);
    transition: background-color .3s ease-out;
    box-shadow: 0 4px 8px rgba(0, 0, 0, .08);
    z-index: 100
}

.dashboard-header .bv_mainselect {
    padding: 15px 30px;
    color: rgba(var(--primary-color), 1);
    width: 100%;
    white-space: nowrap;
    transition: color .3s
}

@media screen and (min-width:1280px) {
    .dashboard-header .bv_mainselect {
        padding: 10px 15px;
        width: 150px
    }
}

.dashboard-header .bv_mainselect:hover {
    color: #24ac80
}

.dashboard-header .bv_mainselect:hover .arrow {
    border-color: #24ac80
}

.dashboard-header .bv_atual {
    font-weight: 500
}

.dashboard-header .bv_ul_inner {
    min-width: 120px;
    background-color: rgba(var(--dropdown-bg-color), 1)
}

.dashboard-header .bv_ul_inner li+li {
    border-top: 1px solid rgba(var(--primary-color), .1)
}

.dashboard-header .bv_ul_inner li {
    color: rgba(var(--primary-color), 1);
    transition: color .3s
}

.dashboard-header .bv_ul_inner li:hover {
    color: #24ac80;
    background-color: transparent
}

.dashboard-header .arrow {
    border: solid rgba(var(--primary-color), 1);
    border-width: 0 1px 1px 0
}

.dashboard-header__content {
    position: relative;
    max-width: 1460px;
    margin: 0 auto;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;
    align-items: center
}

.dashboard-header__logo {
    width: 120px;
    margin-left: 40px
}

@media screen and (min-width:1280px) {
    .dashboard-header__logo {
        margin-left: 0
    }
}

.dashboard-header__logo-text {
    filter: var(--logo-filter)
}

.dashboard-header__nav {
    display: flex;
    flex-grow: 1;
    justify-content: flex-end;
    align-items: center
}

.dashboard-header__menu {
    display: grid;
    align-content: space-between;
    position: fixed;
    flex: 1;
    height: var(--app-height);
    width: 100%;
    top: 0;
    left: 0;
    padding: 60px 0 40px;
    transform: translateX(-100%);
    transition: transform ease .5s;
    background-color: rgba(var(--theme-bg-secondary-color), 1);
    z-index: 10
}

@media screen and (min-width:768px) {
    .dashboard-header__menu {
        width: 50%
    }
}

@media screen and (min-width:1280px) {
    .dashboard-header__menu {
        position: relative;
        max-width: 100%;
        height: auto;
        background-color: transparent;
        transform: translateX(0);
        padding: 0
    }
}

.dashboard-header__menu--active {
    transform: translateX(0);
    transition: transform ease .5s
}

@media screen and (min-width:1280px) {
    .dashboard-header__menu-item-wrap {
        display: flex;
        flex-wrap: wrap;
        align-items: center
    }
}

.dashboard-header__menu-list {
    background-color: rgba(var(--theme-bg-secondary-color), 1)
}

@media screen and (min-width:1280px) {
    .dashboard-header__menu-list {
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: transparent
    }

    .dashboard-header__menu-item {
        margin: 0 15px
    }
}

.dashboard-header__menu-link {
    display: block;
    padding: 15px 25px;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    text-transform: uppercase;
    color: rgba(var(--primary-color), .6);
    transition: border .3s, color .3s;
    border-left: 3px solid transparent
}

@media screen and (min-width:1280px) {
    .dashboard-header__menu-link {
        padding: 20px 0;
        border-left: none;
        border-bottom: 3px solid transparent
    }
}

.dashboard-header__menu-link--active {
    color: rgba(var(--primary-color), 1);
    background-color: rgba(var(--primary-color), .05);
    border-color: #24ac80
}

@media screen and (min-width:1280px) {
    .dashboard-header__menu-link--active {
        background-color: transparent
    }
}

.dashboard-header__menu-link:hover {
    color: #24ac80
}

.dashboard-header__menu-link--active:hover {
    color: rgba(var(--primary-color), 1);
    cursor: initial
}

.dashboard-header__menu-item+.dashboard-header__menu-item {
    border-top: 1px solid rgba(var(--primary-color), .1)
}

@media screen and (min-width:1280px) {
    .dashboard-header__menu-item+.dashboard-header__menu-item {
        border: 0
    }
}

.dashboard-header__user-menu-wrap {
    padding: 10px;
    display: grid;
    grid-template-columns: 20px 28px;
    gap: 15px;
    align-items: center
}

@media screen and (min-width:768px) {
    .dashboard-header__user-menu-wrap {
        grid-template-columns: 20px 250px
    }
}

.dashboard-header__notify {
    align-items: center;
    color: rgba(var(--primary-color), 1);
    transition: color .3s;
    cursor: pointer
}

.dashboard-header__notify, .dashboard-header__notify-img {
    position: relative;
    display: flex
}

.dashboard-header__notify-img--active:after {
    content: '';
    position: absolute;
    top: 3px;
    right: 1px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: #24ac80;
    border: 1px solid rgba(var(--theme-bg-color), 1)
}

.dashboard-header__notify-icon {
    cursor: pointer
}

.dashboard-header__notify-content {
    position: absolute;
    width: 100%;
    min-width: 250px;
    top: 50px;
    right: -50px;
    z-index: 100;
    transform: scaleY(0);
    transform-origin: top;
    opacity: 0;
    visibility: hidden;
    transition: all .3s
}

@media screen and (min-width:768px) {
    .dashboard-header__notify-content {
        right: -100px
    }
}

.dashboard-header__notify-list {
    max-height: 300px;
    border-radius: 10px;
    background-color: rgba(var(--dropdown-bg-color), 1)
}

.dashboard-header__notify-item {
    padding: 15px
}

.dashboard-header__notify-item:not(:first-child) {
    border-top: 1px solid rgba(var(--primary-color), .1)
}

.dashboard-header__notify--active .dashboard-header__notify-content {
    opacity: 1;
    visibility: visible;
    transform: scaleY(1)
}

.dashboard-header__menu-toggle {
    position: absolute;
    top: 5px;
    left: 10px;
    width: 20px;
    height: 20px;
    margin: 10px;
    background: 0 0;
    border: 0;
    z-index: 100
}

.dashboard-header__menu-toggle:after, .dashboard-header__menu-toggle:before {
    opacity: 0;
    position: absolute;
    content: '';
    top: 4px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: rgba(var(--primary-color), 1);
    border-radius: 10px;
    transition: transform .3s, opacity .3s
}

.dashboard-header__menu-toggle:after {
    top: 14px
}

@media screen and (min-width:768px) {
    .dashboard-header__menu-toggle {
        top: 10px
    }
}

@media screen and (min-width:1280px) {
    .dashboard-header__menu-toggle {
        display: none
    }
}

.dashboard-header__menu-toggle--active:after, .dashboard-header__menu-toggle--active:before {
    opacity: 1;
    top: 10px;
    transform: rotate(45deg);
    background-color: rgba(var(--primary-color), .5)
}

.dashboard-header__menu-toggle--active:after {
    transform: rotate(-45deg)
}

.dashboard-header__menu-toggle--active .dashboard-header__menu-toggle-line {
    opacity: 0
}

.dashboard-header__menu-toggle-line {
    position: absolute;
    top: 0;
    left: 0;
    transition: opacity .3s
}

.dashboard-header__menu-toggle-icon {
    fill: rgba(var(--primary-color), 1)
}

.dropdown-menu {
    position: relative;
    color: rgba(var(--primary-color), 1);
    font-weight: 500;
    background-color: rgba(var(--dropdown-bg-color), 1);
    border-radius: 50px;
    cursor: pointer
}

.dropdown-menu__user-info {
    display: grid;
    grid-template-columns: auto auto;
    align-items: center
}

@media screen and (min-width:768px) {
    .dropdown-menu__user-info {
        padding: 4px
    }
}

.dropdown-menu__user-info .down {
    margin-top: 2px;
    right: 20px
}

.dropdown-menu__user-name {
